
.tabs {
  height: 6rem !important;
  padding-top: 0 !important;
  background: #333;
}
.tabs .vux-tab-container {
  height: 6rem;
}
.tabs .vux-tab {
  height: inherit;
  background-color: #333;
}
.tabs .vux-tab .vux-tab-item {
  background: #333;
  line-height: 1;
  color: #fff;
}
.tabs .vux-tab .vux-tab-ink-bar {
  bottom: 0;
}
.gold_color {
  color: #EEB556;
}
.pages {
  padding-top: 44px;
  /* margin-bottom: 55px; */
}
.fullBox {
  position: fixed;
  top: 44px;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  background: #ffffff;
}
.vux-header {
  height: 44px !important;
  background: #317df4 !important;
}
.vux-header .vux-header-left .vux-header-back {
  color: #fff !important;
}
.vux-header .vux-header-left .left-arrow:before {
  border-color: #fff !important;
}
.weui-cells {
  margin-top: 0 !important;
}
.weui-dialog__btn_primary {
  color: #317df4 !important;
}
.weui-tabbar {
  position: fixed !important;
  left: 0;
  right: 0;
}
.weui-tabbar__item.weui-bar__item_on .weui-tabbar__icon,
.weui-tabbar__item.weui-bar__item_on .weui-tabbar__icon > i,
.weui-tabbar__item.weui-bar__item_on .weui-tabbar__label {
  color: #317df4 !important;
}
.slide-right-enter-active,
.slide-right-leave-active,
.slide-left-enter-active,
.slide-left-leave-active {
  will-change: transform;
  transition: all 0.5s cubic-bezier(0.55, 0, 0.1, 1);
  position: absolute;
  width: 100%;
  left: 0;
}
.slide-left-enter,
.slide-right-leave-active {
  opacity: 0;
  transform: translate(30px, 0);
}
.slide-left-leave-active,
.slide-right-enter {
  opacity: 0;
  transform: translate(-30px, 0);
}
/* .slide-left-enter,.slide-right-leave-active {
    transform: translateX(-100%);
}
.slide-right-enter,.slide-left-leave-active {
    transform: translateX(100%);
} */
.van-collapse-item__content {
  font-size: .8rem;
  color: #333;
  line-height: 2rem;
}
.vux-tab .vux-tab-item.vux-tab-selected {
  font-weight: 600;
}
.weui-flex {
  display: -ms-flexbox;
  display: flex;
  padding-bottom: 1.5rem;
}
.weui-flex .weui-flex__item {
  -ms-flex: 1;
      flex: 1;
}
.weui-flex .weui-flex__item .placeholder {
  position: relative;
  text-align: center;
}
.weui-flex .weui-flex__item .placeholder .weui-flex-img {
  width: 2.5rem;
  display: inline-block;
}
.weui-flex .weui-flex__item .placeholder .weui-flex-idot {
  width: 1.5rem;
  height: 1rem;
  background: #333;
  font-size: .6rem;
  border-radius: 10px;
  color: #fff;
  line-height: 1rem;
  position: absolute;
  top: 0;
  right: 25%;
}
.weui-flex .weui-flex__item .placeholder .weui-title {
  color: #333;
  font-size: .8rem;
}



